.footer{
    background: linear-gradient(90deg, #7debf2, #60a4ff);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    width: 100%;
    height: 5vh;
    font-family: 'Jura', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.tittle_footer{
    text-align: center;
    margin: 0;
    padding: 0;
    font-size: 1.2vw;

}