.container_knowledge{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.knowledge{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1em;
    grid-template-rows: 10% 85%;
    height: 75rem;
 }

.list_knowledge{
    grid-column: 1/3;
    height: 85vh;
     display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.section_knowledge{
     width: 80%;
    height: 83vh;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 40vh 40vh;
    margin: 0;
    padding: 0;
    font-family: 'Jura', sans-serif;
}
.card_knowledge{
    align-self: center;
    justify-self: center;
    width: 9vw;
    height: 22vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    /* MODIFICAR COLORES FINALES */
    background: rgba(63, 145, 255, 0.62);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(14px);
    -webkit-backdrop-filter: blur(14px);
    border: 1px solid rgba(63, 145, 255, 0.56);
}
.tittle_card{
    width: 9vw;
    text-align: center;
    margin: 10px 0;
    background: linear-gradient(90deg, #7debf2, #60a4ff);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Jura', sans-serif;
    font-size: 1.5vw;
}
.icon_inside{
    -webkit-filter: invert(100%);
    filter: invert(100%);
    width: 5vw;
    height: 8.5vh;
}

.modal{
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    background: rgba(130, 182, 252, 0.76);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(14px);
    -webkit-backdrop-filter: blur(14px);
    border: 1px solid rgba(130, 182, 252, 0.56);
    display: flex;
    justify-content: center;
    align-items: center;
}
.knowledge_modal{
    width: 35vw;
    padding: 1.5vw;
    height: 65vh;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    background: rgba(16, 99, 211, 0.8);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(14px);
    -webkit-backdrop-filter: blur(14px);
    border: 1px solid rgba(16, 99, 211, 0.56);
}
.tittle_card_modal{
    width: 100%;
    font-size: 4vw;
    text-align: center;
    text-align: center;
    background: linear-gradient(90deg, #7debf2, #60a4ff);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Jura', sans-serif;
}
.icon_inside_modal{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 13vw;
    height: 25vh;
}
.text_modal{
    text-align: center;
    font-size: 0.8vw;
    background: linear-gradient(90deg, #7debf2, #60a4ff);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Jura', sans-serif;
}
.modal_btn{
    display: block;
    top: 0;
    right: 0;
    position: absolute;
    margin: 10px 10px 0 0;
    border-radius: 50%;
    
}
.modal_btn_close{
    width: 2vw
}
.btn_certificate{
    width: 200px;
    /* From https://css.glass */
    background: rgba(43, 227, 227, 0.76);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(14.3px);
    -webkit-backdrop-filter: blur(14.3px);
    border: 1px solid rgba(43, 227, 227, 0.57);
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.1);
    z-index: 100;
    display: flex;
}
